/* index.css */
html, body, #root {
    height: 100%;
    margin: 0;
  }
  
  .container {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
  
  .page {
    position: absolute;
    width: 100%;
    height: 100%;
    transition: transform 0.5s ease-in-out;
  }
  
  /* 當然這裡的 .slide-enter 和 .slide-exit 要加到你的每個組件上 */
  .page-enter {
    transform: translateX(100%);
  }
  
  .page-enter-active {
    transform: translateX(0);
  }
  
  .page-exit {
    transform: translateX(0);
  }
  
  .page-exit-active {
    transform: translateX(-100%);
  }
  
